import React, { Component } from "react";

class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            text: "Komunikat",
        };
    }

    render() {
        return (
            <div className="header">
                {this.state.text}
                <div className="logo">
                    <img
                        src="/assets/SplashScreen.scale-200.png"
                        alt="logo"
                        width="200"
                    />
                </div>
            </div>
        );
    }
}

export default Header;
