import React, { Component } from "react";
import { Link } from "react-router-dom";
import Header from "./Header";
import Input from "./Input";

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showSettingsList: false,
            value: "",
            data: [
                "monitor",
                "program",
                "application",
                "keyboard",
                "javascript",
                "gaming",
                "network",
                "monitor",
                "program",
                "application",
                "keyboard",
                "javascript",
                "gaming",
                "network",
            ],
        };
        this.handleValueChange = this.handleValueChange.bind(this);
        this.hide = this.hide.bind(this);
        this.show = this.show.bind(this);
    }

    getImage(name) {
        const images = require.context("../../public/assets", true);

        const image = images("./" + name + ".png");

        return image;
    }

    handleValueChange(event) {
        this.setState({ value: event });
    }

    hide() {
        this.setState({ showSettingsList: false });
    }

    show() {
        this.setState({ showSettingsList: true, value: "" });
    }

    selectSettings(e, item) {
        this.hide();
        console.log(item);
        //this.setState({ value: name });
    }

    render() {
        const list = this.state.data
            .filter((item) =>
                item
                    .toLocaleLowerCase()
                    .includes(this.state.value.toLocaleLowerCase())
            )
            .map((item, index) => (
                <li
                    key={index}
                    name={item}
                    onClick={(e) => {
                        this.setState({ value: e.target.getAttribute("name") });
                        this.hide();
                    }}
                >
                    {item}
                </li>
            ));

        return (
            <div className="home">
                {/* <Header /> */}
                <div className="section">
                    <div className="section-title">
                        <img
                            src="/assets/settings-icon.png"
                            alt="logo"
                            width="80"
                        />
                        <p>Process settings</p>
                    </div>
                    <div className="new-settings">
                        Select settings or{" "}
                        <Link to="/new-settings">enter new settings</Link>
                    </div>
                    <div className="select-existing-settings">
                        <Input
                            value={this.state.value}
                            onChange={this.handleValueChange}
                            onBlur={this.hide}
                            onFocus={this.show}
                        />
                        {this.state.showSettingsList ? <ul>{list}</ul> : null}
                    </div>
                </div>
                <div className="menu">
                    <div className="homing">
                        <img src="/assets/homing.png" alt="homing" width="80" />
                        <p>Homing</p>
                    </div>
                    <div className="start-polishing">
                        <img
                            src="/assets/start-polishing.png"
                            alt="start-polishing"
                            width="80"
                        />
                        <p>Start polishing</p>
                    </div>
                </div>
            </div>
        );
    }
}

export default Home;
